import Vue from 'vue';

const incomeStore = {
	namespaced: true,
	state: {
		currentIncome: {},
		income: {
			2019: {},
			2020: {
				1: {
					category: 'Зарплата',
					currency: "RUB",
					date: "08.10.2020, 10:00:00",
					dateTime: "08.10.2020, 10:00:00",
					value: 40000,
					commentary: 'Зпшка',
					dataFull: 'Thu Oct 08 2020 10:00:00 GMT+0300 (Москва, стандартное время)',
				},
				2: {
					category: 'Аванс',
					currency: "RUB",
					date: "30.01.2020, 00:00:00",
					dateTime: "30.01.2020, 10:00:00",
					value: 30000,
					commentary: 'Аванс',
					dataFull: 'Thu Jan 30 2020 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
			},
			2021: {
				1: {
					category: 'Зарплата',
					currency: "RUB",
					date: "11.10.2021, 00:00:00",
					dateTime: "11.10.2021, 11:00:00",
					value: 40000,
					commentary: 'Зпшка октябрь',
					dataFull: 'Thu Oct 11 2021 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
				2: {
					category: 'Аванс',
					currency: "RUB",
					date: "31.10.2021, 00:00:00",
					dateTime: "31.10.2021, 09:00:00",
					value: 20000,
					commentary: 'Мини зпшка сентябрь',
					dataFull: 'Thu Oct 31 2021 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
				3: {
					category: 'Аванс',
					currency: "RUB",
					date: "30.08.2021, 00:00:00",
					dateTime: "30.08.2021, 00:00:00",
					value: 20000,
					commentary: 'Мини зпшка апрель',
					dataFull: 'Thu Aug 30 2021 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
				4: {
					category: 'Аванс',
					currency: "RUB",
					date: "08.10.2021, 00:00:00",
					dateTime: "08.10.2021, 11:00:00",
					value: 30000,
					commentary: 'Аванс октябрь',
					dataFull: 'Thu Oct 08 2021 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
				5: {
					category: 'Аванс',
					currency: "RUB",
					date: "05.10.2021, 00:00:00",
					dateTime: "05.10.2021, 11:00:00",
					value: 20000,
					commentary: 'Аванс октябрь',
					dataFull: 'Thu Oct 05 2021 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
			},
			2022: {},
		},
		optionsCategory: [
			{ text: "Зарплата", value: "Зарплата" },
			{ text: "Премия", value: "Премия" },
		],
		currentYear: (new Date()).getFullYear(),
		yearSelectedByTheUser: (new Date()).getFullYear(),
		monthSelectedByTheUser: (new Date()).getMonth() + 1,
		daySelectedByTheUser: (new Date()).getDate(),
		startWeekSelectedByTheUser: new Date(),
		startRangeDateSelectedByTheUser: 0,
		endRangeDateSelectedByTheUser: 0,
		// changeType: new Date().toLocaleString().slice(3, 5),
		changeType: 'Месяц',
	},
	getters: {
		getCurrentYear(state) {
			// Возвращаю текущий год
			return state.currentYear;
		},
		filterArr(state) {
			// Возвращаю все доходы за нужный юзеру год

			Object.keys(state.income).forEach(el => {
				if (el == state.yearSelectedByTheUser) {
					state.currentIncome = state.income[el]
				}
			})

			return state.currentIncome;
		},
		getIncome(state) {
			// Прокидываю данные в виде массива с объектами
			Object.keys(state.income).forEach(el => {
				if (el == state.currentYear) {
					state.currentIncome = state.income[el]
				}
			})

			return state.currentIncome
		},
		getIncomeAmount(state, getters) {
			// Расчет доходов за месяц
			let date = new Date().toLocaleString().split(',')[0].slice(3, 5);

			let sum = 0;
			Object.values(getters.getIncome).forEach(el => {
				if (date == el.date.split(',')[0].slice(3, 5)) {
					sum += el.value;
				}
			})
			return sum
		},
		getOptionsCategory(state) {
			// Прокидываю категории в селекте доходов
			return state.optionsCategory;
		},
		getFullIncomeYears(state) {
			// Полная сумма доходов за всё время
			let arr = [];
			Object.values(state.income).forEach(el => {
				arr.push(Object.values(el).reduce((sum, elem) => sum + elem.value, 0));
			})

			return arr.reduce((sum, elem) => sum += elem);
		},
		getFullIncome(state, getters) {
			// Полная сумма доходов за год
			return Object.values(state.currentIncome).reduce((sum, el) => sum += el.value, 0);
		},
		getFullIncomeArrMonths(state, getters) {
			// Полная сумма доходов за каждый месяц
			let arr = [];

			function f1(value) {
				let sum = 0;
				let obj = {};

				Object.values(getters.filterArr).forEach(el => {
					if (el.date.split(',')[0].slice(3, 5) == value) {
						sum += el.value;
					}
				})
				obj[value] = sum;
				return obj;
			}

			arr.push(f1('01'))
			arr.push(f1('02'))
			arr.push(f1('03'))
			arr.push(f1('04'))
			arr.push(f1('05'))
			arr.push(f1('06'))
			arr.push(f1('07'))
			arr.push(f1('08'))
			arr.push(f1('09'))
			arr.push(f1('10'))
			arr.push(f1('11'))
			arr.push(f1('12'))

			return arr;

		},
		getFullIncomeValueCategory(state, getters) {
			return Object.values(getters.getFullIncomeForEachCategory).reduce((sum, el) => sum + el, 0)
		},
		getFullIncomeForEachCategory(state, getters) {
			let arr = [];
			let obj = {};

			function f1(arr) {
				arr.forEach(el => {
					if (obj[el.category] == undefined) {
						obj[el.category] = el.value;
					} else {
						obj[el.category] = obj[el.category] + el.value;
					}
				})
			}

			if (state.changeType == 'Неделя') {
				let date = new Date();
				arr = [];

				Date.prototype.getWeek = function () {
					var date = new Date(this.getTime());
					date.setHours(0, 0, 0, 0);
					date.setDate(date.getDate() + 3 - (date.getDay() + 6) % 7);
					var week1 = new Date(date.getFullYear(), 0, 4);
					return 1 + Math.round(((date.getTime() - week1.getTime()) / 86400000
						- 3 + (week1.getDay() + 6) % 7) / 7);
				}

				Date.prototype.getWeekEl = function (dataEl) {
					var date = dataEl;
					date.setHours(0, 0, 0, 0);
					date.setDate(date.getDate() + 3 - (date.getDay() + 6) % 7);
					var week1 = new Date(date.getFullYear(), 0, 4);
					return 1 + Math.round(((date.getTime() - week1.getTime()) / 86400000
						- 3 + (week1.getDay() + 6) % 7) / 7);
				}

				Object.values(getters.filterArr).forEach(el => {
					if (date.getWeekEl(new Date(state.startWeekSelectedByTheUser)) == date.getWeekEl(new Date(el.dataFull))) {
						arr.push(el);
					}
				})
			} else if (state.changeType == 'День') {
				arr = [];
				Object.values(getters.filterArr).forEach(el => {
					if (state.monthSelectedByTheUser == el.date.slice(3, 5) && state.daySelectedByTheUser == el.date.slice(0, 2)) {
						arr.push(el)
					}
				})
			}
			else if (state.changeType == 'Месяц') {
				arr = [];
				Object.values(getters.filterArr).forEach(el => {
					if (state.monthSelectedByTheUser == el.date.slice(3, 5)) {
						arr.push(el)
					}
				})
			}
			else if (state.changeType == 'Год') {
				arr = [];
				Object.values(getters.filterArr).forEach(el => {
					if (el.date.slice(6, 10) == state.yearSelectedByTheUser) {
						arr.push(el)
					}
				})
			}
			else if (state.changeType == 'Период') {
				arr = [];
				let res = [];

				let startDateYear = state.startRangeDateSelectedByTheUser.toLocaleString().slice(6, 10);
				let startDateMonth = state.startRangeDateSelectedByTheUser.toLocaleString().slice(3, 5);
				let startDateDays = state.startRangeDateSelectedByTheUser.toLocaleString().slice(0, 2);

				let endDateYear = state.endRangeDateSelectedByTheUser.toLocaleString().slice(6, 10);
				let endDateMonth = state.endRangeDateSelectedByTheUser.toLocaleString().slice(3, 5);
				let endDateDays = state.endRangeDateSelectedByTheUser.toLocaleString().slice(0, 2);

				Object.keys(state.income).forEach(elem => {
					if (+elem >= +startDateYear && +elem <= endDateYear) {
						if (Object.keys(state.income[elem]).length >= 1) {
							res.push(state.income[elem]);
						}

					}
				})

				res.forEach(elem => {
					Object.values(elem).forEach(el => {
						if (+el.date.slice(3, 5) >= +startDateMonth && +el.date.slice(3, 5) <= +endDateMonth) {
							if (+el.date.slice(0, 2) >= +startDateDays && +el.date.slice(0, 2) <= +endDateDays) {
								arr.push(el)
							}
						}
					})
				})
			}
			else if (state.changeType == 'fullTime') {
				// let arr = [];
				Object.values(state.income).forEach(el => {
					Object.values(el).forEach(elem => {
						arr.push(elem);
					})
				})
			} else {
				Object.values(getters.filterArr).forEach(el => {
					if (new Date().toLocaleString().slice(3, 5) == el.date.slice(3, 5)) {
						arr.push(el)
					}
				})
			}
			f1(arr);
			return obj
		}
	},
	mutations: {
		ADD_NEW_INCOME(state, incomeAct) {
			// Записываю новый доход в исходный объект
			Vue.set(state.income[incomeAct.date.split(',')[0].slice(6, 11)], incomeAct.id, incomeAct);
		},
		ADD_NEW_YEAR(state, year) {
			state.yearSelectedByTheUser = year;
		},
		ADD_NEW_MONTH(state, arr) {
			// console.log(state)
			// console.log(arr)
			state.yearSelectedByTheUser = arr[0];
			state.monthSelectedByTheUser = arr[1];
		},
		ADD_NEW_DAY(state, arr) {
			state.yearSelectedByTheUser = arr[0];
			state.monthSelectedByTheUser = arr[1];
			state.daySelectedByTheUser = arr[2];
		},
		ADD_NEW_WEEK(state, arr) {
			state.yearSelectedByTheUser = arr[0];
			state.monthSelectedByTheUser = arr[1];
			state.startWeekSelectedByTheUser = arr[2];
		},
		ADD_NEW_CANGE_TYPE(state, type) {
			state.changeType = type;
		},
		ADD_NEW_RANGE(state, arr) {
			state.startRangeDateSelectedByTheUser = arr[0];
			state.endRangeDateSelectedByTheUser = arr[1];
		},

	},
	actions: {
		setNewIncome(context, newIncome) {
			// Получаю данные из формы введенные пользователем, добавляю им id и прокидываю дальше в мутации для дальнейшей записи в исходный объект
			const newIncomePlusId = { ...newIncome, id: Object.values(context.state.income[newIncome.date.split(',')[0].slice(6, 11)]).length + 1 };
			context.commit('ADD_NEW_INCOME', newIncomePlusId);
		},
		setNewChangeGapType(context, newType) {
			context.commit('ADD_NEW_CANGE_TYPE', newType);
		},
		setNextYear2(context, year) {
			context.commit('ADD_NEW_YEAR', year)
		},
		setPreviousYear2(context, year) {
			context.commit('ADD_NEW_YEAR', year)
		},
		setPrevMonth(context, month) {
			context.commit('ADD_NEW_MONTH', month)
		},
		setPrevDay(context, day) {
			context.commit('ADD_NEW_DAY', day)
		},
		setPrevWeek(context, week) {
			context.commit('ADD_NEW_WEEK', week)
		},
		setPrevRange(context, arr) {
			context.commit('ADD_NEW_RANGE', arr)
		}
	}
};

export default incomeStore;