<template>
  <div class="container">
    <div class="section__ind new">
      <el-header>
        <el-page-header
          content="Диаграмма"
          title="Назад"
          @back="$router.push({ name: 'AllIncome' })"
        ></el-page-header>
      </el-header>
      <div class="chart">
        <div class="chart__info">
          <h3>Доходы</h3>
          {{ daySelectedByTheUser() }}.{{ monthSelectedByTheUser() }}.{{
            yearSelectedByTheUser()
          }}
        </div>
        <div class="chart__block">
          <div class="container">
            <bar-chart
              :data="barChartData"
              :options="barChartOptions"
              :height="200"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapState } from "vuex";
import BarChart from "@/components/chartInc";

const chartColors = {
  red: "rgb(255, 99, 132)",
  orange: "rgb(255, 159, 64)",
  yellow: "rgb(255, 205, 86)",
  green: "rgb(75, 192, 192)",
  blue: "rgb(54, 162, 235)",
  purple: "rgb(153, 102, 255)",
  grey: "rgb(201, 203, 207)",
};

export default {
  name: "ChartIncome",
  components: {
    BarChart,
  },
  data: () => ({
    ...mapState("income", [
      "daySelectedByTheUser",
      "monthSelectedByTheUser",
      "yearSelectedByTheUser",
    ]),
    barChartData: {
      labels: ["Jan", "Feb", "Mar"],
      datasets: [
        {
          label: "Income",
          // backgroundColor: ["red", "orange", "yellow"],
          backgroundColor: [
            chartColors.red,
            chartColors.orange,
            chartColors.yellow,
          ],
          data: [10, 15, 20],
        },
      ],
    },
    barChartOptions: {
      responsive: true,
      legend: {
        display: true,
        position: "bottom",
      },
      tooltips: {
        // position: "nearest",
        callbacks: {
          labelColor: function (tooltipItem, chart) {
            return {
              borderColor: "rgb(255, 255, 0)",
              backgroundColor: "rgb(255, 0, 0)",
              intersect: false,
            };
          },
        },
      },
      scales: {
        yAxes: [
          {
            ticks: {
              beginAtZero: true,
            },
          },
        ],
      },
    },
  }),
  computed: {},
  methods: {},
};
</script>

<style>
@media (max-width: 577px) {
}
</style>
