<template>
  <div class="container">
    <div class="section__ind new">
      <el-header>
        <el-page-header
          content=""
          title="Назад"
          @back="$router.push({ name: 'Main' })"
        ></el-page-header>
        <el-select
          v-model="value"
          placeholder=""
          class="type-period-select"
          @change="changeGapType"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        <div class="chart-ic__block">
          <!-- <div class="chart-ic" @click="$router.push({ name: 'ChartIncome' })"> -->
          <div class="chart-ic">
            <i class="el-icon-data-line"></i>
          </div>
        </div>
      </el-header>

      <el-carousel
        arrow="never"
        :autoplay="false"
        indicator-position="none"
        height="700px"
      >
        <el-carousel-item v-for="item in 1" :key="item">
          <div class="carousel__top" v-if="carouselTop == 'День'">
            <el-date-picker
              type="date"
              placeholder=""
              v-model="dateDay"
              style="width: 100%"
              @change="sortingByDay"
            ></el-date-picker>
            {{ overwriteCurrentYear }}
          </div>
          <div class="carousel__top" v-if="carouselTop == 'Неделя'">
            <el-date-picker
              v-model="dateWeek"
              type="week"
              format="WW неделя"
              placeholder=""
              @change="sortingByWeek"
              class="getWeek"
              :picker-options="dateRangeOptions1"
            >
            </el-date-picker>
            {{ overwriteCurrentYear }}
          </div>
          <div class="carousel__top" v-if="carouselTop == 'Месяц'">
            <el-date-picker
              type="date"
              placeholder=""
              v-model="dateMonth"
              style="width: 100%"
              @change="sortingByMonth"
            ></el-date-picker>
            {{ overwriteCurrentYear }}
          </div>
          <div class="carousel__top" v-if="carouselTop == 'Год'">
            <h3>{{ year }}</h3>
            {{ overwriteCurrentYear }}
            <div class="years">
              <el-button
                @click="previousYear"
                type="info"
                icon="el-icon-arrow-left"
                circle
              ></el-button>
              <el-button
                @click="nextYear"
                type="info"
                icon="el-icon-arrow-right"
                circle
              ></el-button>
            </div>
          </div>
          <div class="carousel__top" v-if="carouselTop == 'Период'">
            <el-date-picker
              v-model="dateRange"
              type="daterange"
              range-separator="По"
              start-placeholder="С этой даты"
              end-placeholder="Эту дату"
              @change="sortingByRange"
              :picker-options="dateRangeOptions1"
            >
            </el-date-picker>
            {{ overwriteCurrentYear }}
          </div>

          <div class="carousel__full-wrap">
            <div class="carousel__full">
              <h4>Всего:</h4>
              <p>{{ getFullIncomeValueCategory }}</p>
            </div>
          </div>
          <div class="carousel__wrap">
            <div class="carousel__month">
              <div
                v-for="(item, value) in getFullIncomeForEachCategory"
                :key="item.text"
              >
                <div>
                  <p>{{ value }}</p>
                </div>
              </div>
            </div>
            <div class="carousel__val">
              <div v-for="item in getFullIncomeForEachCategory" :key="item.key">
                <p>{{ item }}</p>
              </div>
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>
      <!-- {{ getFullIncomeForEachCategory }}
      {{ getFullIncomeValueCategory }} -->
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "AllIncome",
  components: {},
  data: () => ({
    dateRangeOptions1: {
      firstDayOfWeek: 1,
    },
    options: [
      {
        value: "День",
        label: "День",
      },
      {
        value: "Неделя",
        label: "Неделя",
      },
      {
        value: "Месяц",
        label: "Месяц",
      },
      {
        value: "Год",
        label: "Год",
      },
      {
        value: "Период",
        label: "Период",
      },
      {
        value: "fullTime",
        label: "Все время",
      },
    ],
    value: "Месяц",
    year: 0,
    month: new Date().getMonth() + 1,
    carouselTop: "Месяц",
    dateMonth: new Date(),
    dateDay: new Date(),
    dateWeek: new Date(),
    dateRange: "",
    // changeTypeDate: "",
  }),

  computed: {
    ...mapGetters("income", [
      "getFullIncomeArrMonths",
      "getFullIncome",
      "getOptionsCategory",
      "getFullIncomeForEachCategory",
      "getFullIncomeValueCategory",
    ]),
    ...mapGetters("expenses", [
      "getFullExpensesArrMonths",
      "getFullCost",
      "filterArr",
      "getCurrentYear",
    ]),
    overwriteCurrentYear() {
      this.year = this.getCurrentYear;
    },
  },
  methods: {
    ...mapActions("expenses", ["setPreviousYear", "setNextYear"]),
    ...mapActions("income", [
      "setPreviousYear2",
      "setNextYear2",
      "setNewChangeGapType",
      "setPrevMonth",
      "setPrevDay",
      "setPrevWeek",
      "setPrevRange",
    ]),
    sortingByMonth(e) {
      // console.log(e.getMonth());
      let arr = [];
      arr.push(this.dateMonth.getFullYear());
      arr.push(this.dateMonth.getMonth() + 1);
      this.setPrevMonth(arr);
    },
    sortingByDay(e) {
      // console.log(e);
      let arr = [];
      arr.push(this.dateDay.getFullYear());
      arr.push(this.dateDay.getMonth() + 1);
      arr.push(this.dateDay.getDate());
      this.setPrevDay(arr);
    },
    sortingByWeek() {
      let arr = [];
      arr.push(this.dateWeek.getFullYear());
      arr.push(this.dateWeek.getMonth() + 1);
      arr.push(this.dateWeek);
      this.setPrevWeek(arr);
    },
    sortingByRange() {
      // console.log(this.dateRange);
      this.setPrevRange(this.dateRange);
    },
    previousYear() {
      this.year--;
      this.setPreviousYear(this.year);
      this.setPreviousYear2(this.year);
    },
    nextYear() {
      this.year++;
      this.setNextYear(this.year);
      this.setNextYear2(this.year);
    },
    changeGapType(e) {
      this.setNewChangeGapType(e);
      // console.log(e);
      if (e == "fullTime") {
        this.carouselTop = false;
      } else if (e == "День") {
        this.carouselTop = "День";
      } else if (e == "Неделя") {
        this.carouselTop = "Неделя";
      } else if (e == "Месяц") {
        this.carouselTop = "Месяц";
      } else if (e == "Период") {
        this.carouselTop = "Период";
      } else if (e == "Год") {
        this.carouselTop = "Год";
      } else {
        this.carouselTop = true;
      }
    },
  },
};
</script>

<style>
.chart-ic__block {
  margin-left: auto;
}
.chart-ic {
  cursor: pointer;
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all ease 0.2s;
}

.chart-ic:hover {
  background-color: rgb(243, 239, 239);
  transition: all ease 0.2s;
}

@media (max-width: 577px) {
}
</style>
