import VueRouter from 'vue-router';

import Main from '../pages/Main';
import AddIncome from '../pages/AddIncome';
import AddExpenses from '../pages/AddExpenses';
import BalanceOut from '../pages/BalanceOut';
import IncomeCategorySettings from '../pages/IncomeCategorySettings';
import ExpensesCategorySettings from '../pages/ExpensesCategorySettings';
import ChartIncome from '../pages/ChartIncome';
import AllIncome from '../pages/AllIncome';
import NotFound from '../pages/404';

export default new VueRouter({
	mode: 'history',
	routes: [
		{
			path: '/Budget',
			name: 'Main',
			component: Main
		},
		{
			path: '/Budget/ChartIncome',
			name: 'ChartIncome',
			component: ChartIncome
		},
		{
			path: '/Budget/AllIncome',
			name: 'AllIncome',
			component: AllIncome
		},
		{
			path: '/Budget/IncomeCategorySettings',
			name: 'IncomeCategorySettings',
			component: IncomeCategorySettings
		},
		{
			path: '/Budget/ExpensesCategorySettings',
			name: 'ExpensesCategorySettings',
			component: ExpensesCategorySettings
		},
		{
			path: '/Budget/AddIncome',
			name: 'AddIncome',
			component: AddIncome
		},
		{
			path: '/Budget/AddExpenses',
			name: 'AddExpenses',
			component: AddExpenses
		},
		{
			path: '/Budget/BalanceOut',
			name: 'BalanceOut',
			component: BalanceOut
		},
		{
			path: '*',
			name: 'NotFound',
			component: NotFound
		},
	]
})