<template>
  <div class="container">
    <div class="section__ind new">
      <el-header>
        <el-page-header
          content="Настройка категорий доходов"
          title="Назад"
          @back="$router.push({ name: 'AddIncome' })"
        ></el-page-header>
      </el-header>

      <el-card class="settingsCategory">
        <el-col
          :xs="24"
          :sm="24"
          :md="24"
          :lg="24"
          :xl="24"
          style="margin-bottom: 20px"
        >
          <!-- <el-form-item class="ind" label="Название новой категории"> -->
          <el-input
            v-model="valueNewCategory"
            style="margin-bottom: 20px"
          ></el-input>
          <el-button type="primary" @click="addNewCategory"
            >Добавить новую категорию</el-button
          >
          <!-- </el-form-item> -->
        </el-col>

        <h3>Список существующих категорий</h3>

        <el-col
          :xs="24"
          :sm="24"
          :md="24"
          :lg="24"
          :xl="24"
          style="margin-bottom: 20px"
        >
          <div
            class="settingsCategory__elem"
            v-for="(elem, i) in getOptionsCategory"
            :key="elem.value"
          >
            {{ elem.text }}

            <div class="settingsCategory__elem-btns">
              <el-button
                type="primary"
                size="mini"
                @click="removeElemCategory(i)"
                >Удалить</el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="(centerDialogVisible = true), myId(i)"
                >Редактировать</el-button
              >
            </div>
            <el-dialog
              title=""
              :visible.sync="centerDialogVisible"
              width="300px"
              center
            >
              <el-input
                placeholder="Введите новое название"
                v-model="inputNewNameElemCategory"
              ></el-input>
              <span slot="footer" class="dialog-footer">
                <el-button type="danger" @click="centerDialogVisible = false"
                  >Отмена</el-button
                >
                <el-button
                  type="primary"
                  @click="(centerDialogVisible = false), newNameElemCategory(i)"
                  >Записать</el-button
                >
              </span>
            </el-dialog>
          </div>
        </el-col>
      </el-card>
    </div>
  </div>
</template>

<script>
// import Income from "@/components/Income";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "IncomeCategorySettings",
  components: {
    // Income,
  },
  data: () => ({
    valueNewCategory: "",
    centerDialogVisible: false,
    inputNewNameElemCategory: "",
    id: 0,
  }),
  computed: {
    ...mapGetters("income", ["getOptionsCategory"]),
  },
  methods: {
    // ...mapActions("income", ["getIncome", "setNewIncome"]),
    addNewCategory() {
      this.getOptionsCategory.push({
        text: this.valueNewCategory,
        value: this.valueNewCategory,
      });
      this.valueNewCategory = "";
    },
    removeElemCategory(i) {
      this.getOptionsCategory.splice(i, 1);
    },
    newNameElemCategory(i) {
      this.getOptionsCategory[this.id].text = this.inputNewNameElemCategory;
      this.getOptionsCategory[this.id].value = this.inputNewNameElemCategory;

      this.inputNewNameElemCategory = "";
    },
    myId(i) {
      this.id = i;
    },
  },
};
</script>

<style>
.settingsCategory h3 {
  margin-bottom: 20px;
}
.settingsCategory__elem {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
}
</style>
